@import url(https://fonts.googleapis.com/css?family=Scheherazade&display=swap);
/*******************
 Box Sizing
 *******************/
body,
html,
span,
button,
div {
  font-family: "Scheherazade", sans-serif;
  font-size: 25px;
}
html {
  box-sizing: border-box;
  direction: rtl;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

/****************************
   Generic Margins and Paddings
   ****************************/
body,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
p,
pre,
blockquote,
figure,
hr {
  margin: 0;
  padding: 0;
}

/*******************
   Lists
   *******************/
ul {
  list-style: none;
}

/*******************
   Forms and buttons
   *******************/
input,
textarea,
select,
button {
  color: inherit;
  font: inherit;
  letter-spacing: inherit;
}

/* I usually expand input and textarea to full-width */
input[type="text"],
textarea {
  width: 100%;
}

/* More friendly border */
input,
textarea,
button {
  border: 1px solid gray;
}

/* Some defaults for one-liner buttons */
button {
  padding: 0.75em 1em;
  border-radius: 0;
  line-height: 1;
  background-color: transparent;
}

button * {
  pointer-events: none;
}

/***********************************
   Easy responsive for media elements
   ***********************************/
img,
svg,
iframe,
video,
object,
embed {
  display: block;
  max-width: 100%;
}

/*******************
   Useful table styles
   *******************/
table {
  table-layout: fixed;
  width: 100%;
}

/*******************
   The hidden attribute
   *******************/
[hidden] {
  display: none !important;
}

/*******************
   Noscript
   *******************/
noscript {
  display: block;
  margin-bottom: 1em;
  margin-top: 1em;
}

/*******************
   Tabindex
   *******************/
[tabindex="-1"] {
  outline: none !important;
}

.calcCont {
  float: right;
  text-align: right;
  width: 100%;
}
.calcRow span {
  display: inline-block;
}
.calcRow {
  background-color: #f0fbfc;
  padding: 5px 35px;
  margin: 5px 10px;
  border-radius: 32px;
}
.calcRow.even {
  background-color: #fff;
}
.dayIndex {
  width: 5%;
}
.dayName {
  width: 10%;
}
.weekName {
  width: 10%;
}
.solarDay {
  width: 15%;
}
.mildadiDay {
  width: 15%;
}
.eventDetails {
  width: 37%;
}
.bold {
  font-weight: 900;
  font-size: 1.1em;
}
.solarDayName {
  display: inline-block;
  font-size: 1.4em;
  margin-left: 9px;
  font-weight: 900;
  background-color: #9fe4ea;
  color: #fff;
  border-radius: 100%;
  width: 43px;
  text-align: center;
  height: 43px;
  line-height: 43px;
}
.navbar {
  position: absolute;
  top: 10px;
  right: 10px;
}
.navbar button {
  outline: none;
  background-color: #fff;
  width: 30px;
  height: 30px;
  line-height: 1px;
  font-size: 39px;
  display: inline-block;
  padding: 0;
  cursor: pointer;
}
.calcHeader {
  background-color: #37c2d0;
  color: #fff;
}
.calcHeader .dayIndex {
  background-color: transparent;
}
.naborDay {
  background-color: #ff9534;
  color: #fff;
  width: 40px;
  text-align: center;
  border-radius: 30px;
}
.panjiDay {
  background-color: #61c15b;
  color: #fff;
  width: 44px;
  text-align: center;
  border-radius: 30px;
}
.event {
  border: 1px solid #37c2d0;
  border-radius: 30px;
  padding: 0 1em;
  margin: 0 10px;
  color: #000;
}
.zMonthlyHoliday {
  border: 1px solid #4caf50;
}
.holiday {
  color: #e91e63;
}
.holiday .solarDayName {
  background-color: #ea7d7d;
}
.zHoliday {
  background-color: #ea7d7d;
  color: #fff;
  border: none;
}
.monthName {
  text-align: center;
  font-size: 2em;
  display: block;
  width: 100px;
  margin: auto;
  border-bottom: 3px solid #37c2d0;
  padding: 0;
  line-height: 53px;
  margin-bottom: 10px;
}

@media print {
  .no-print,
  .no-print * {
    display: none !important;
  }
}

